export default {
	data() {
		return {
			msg: [],
			leng: 0,
			sendTime: 0,
			focusTarget: 0
		};
	},
	computed: {
	},
	watch: {
	},
	onLoad() {
		this.SendMsg()
	},
	methods: {
		nextInput(data){
			console.log(data);
			switch(data){
				case 0:
					this.focusTarget = 1;
					break
				case 1:
					this.focusTarget = 2;
					break
				case 2:
					this.focusTarget = 3;
					break
				default:
					break;
			}
		},
		LoginAccPws(){
			
			uni.redirectTo({
				url: '../login-accpwd/login-accpwd'
			})
		},
		SendMsg(){
			
			if(this.sendTime == 0){
				this.sendTime = 10
				var time = setInterval(() => {
					if(this.sendTime == 0){
						clearInterval(time)
					} else {
						this.sendTime -= 1
						// console.log(this.sendTime);
					}
					
				}, 1000)
				
			}
			
		},
		Login(){
			if(this.msg != ''){
				var msg = this.msg[0] + this.msg[1] + this.msg[2] + this.msg[3]
				uni.reLaunch({
					url: '../index/index'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请输入验证码'
				})
			}
		},
		ClearTel(){
			this.tel = ''
		},
		ViewIntro(){
			uni.navi
		},
	}
}
